*,
*:before,
*:after {
    box-sizing: border-box;
}

.footer {
    display: flex;
    flex-flow: row wrap;
    padding: 30px 30px 20px 30px;
    color: white;
    background-color: #24292e !important;
    border-top: 1px solid #e5e5e5;
}

.footer>* {
    flex: 1 100%;
}

.footer__logo {
    font-family: 'Pacifico', cursive;
    font-weight: 400;
    font-size: 1.5rem;
    margin-bottom: 2rem;
}

.footer__addr h2 {
    margin-top: 0.3em;
    font-size: 15px;
    font-weight: 400;
    font-family: acumin-pro, system-ui, sans-serif;
}

.nav__title {
    font-weight: 400;
    font-size: 18px;
    font-family: acumin-pro, system-ui, sans-serif;
}

.footer address {
    font-style: normal;
    color: #999;
    font-family: acumin-pro, system-ui, sans-serif;
    font-size: 14px;

}

.footer__btn {

    background-color: rgb(33, 33, 33, 0.07);
    border-radius: 100px;
    color: gray;
    line-height: 0;
    font-size: 0.8rem;
    padding: 0.4em 1em 0.6em;
    border: 1px solid goldenrod;
}

.footer ul {
    list-style: none;
    padding-left: 0;
}

.footer li {
    line-height: 2em;
    font-size: 15px;
}

.footer a {
    text-decoration: none;
    color: #999;
}

.footer__nav {
    display: flex;
    flex-flow: row wrap;
}

.footer__nav>* {
    flex: 1 50%;
    margin-right: 0em;
}

.nav__ul--extra {
    column-count: 2;
    column-gap: 0em;
}

.legal {
    display: flex;
    flex-wrap: wrap;
    margin-right: auto;
    color: #999;
    font-size: 15px;
    margin-left: 5.5rem;
    margin-top: -1rem;

}

.ficon {
    max-width: 20%;
    margin-top: 1.3rem;
}

.fcontacticons {
    margin: 2rem;
}

.fcontacticons a {
    padding: .5rem;
    color: grey;
    font-size: 1.5rem;
    transition: all .3s;
}

.fcontacticons a:hover {
    color: goldenrod;
}

.about-links {}

@media screen and (min-width: 24.375em) {
    .legal .legal__links {

        margin-left: auto;
        margin-top: 5rem;
    }
}

@media screen and (min-width: 40.375em) {
    .footer__nav>* {
        flex: 1;
    }

    .nav__item--extra {
        flex-grow: 2;
    }

    .footer__addr {
        flex: 1 0px;
    }

    .footer__nav {
        flex: 2 0px;
    }


}

@media only screen and (max-width:600px) {
    .footer {
        display: flex;
        flex-flow: row wrap;
        padding: 30px 30px 20px 30px;
        color: white;
        background-color: #24292e !important;
        border-top: 1px solid #e5e5e5;
        margin-top: 5rem;
    }

    .ficon {
        max-width: 15%;
        margin-top: 0rem;
    }

    .legal {
        display: flex;
        flex-wrap: wrap;
        margin-right: auto;
        color: #999;
        font-size: 15px;
        margin-left: 0rem;

    }
}